export default {
	data() {
		return {
			cooldownTime:'获取验证码',
			btnDisable:false,
			second:'',
			logUserName:'',
			ResetPassword:'',//重置密码
			ResetPasswordSecond:'',//重置密码重复
			verificationCodeValue:''//验证码
		}
	},
	onLoad() {
		
	},
	methods:{
		closePage(){
			uni.navigateBack({
			    delta: 1
			});
		},
		verificationCode(){//验证码函数
			var THAT = this;
			if(this.logUserName){
				//这里写发送验证码函数
				uni.showToast({
					title:"验证码已发送"
				})
				//倒计时函数
				this.second = 's';
				this.cooldownTime = 60;
				this.btnDisable = true;
				var verTime = setInterval(function(){
					THAT.cooldownTime = THAT.cooldownTime - 1;
					if(THAT.cooldownTime==-1){
						THAT.btnDisable = false;
						THAT.cooldownTime='获取验证码';
						THAT.second='';
						clearInterval(verTime);
					}
				},1000)
			}
			else{
				uni.showToast({
					title:"请输入手机号",
					icon:"none"
				})
			}
		},
	}
}
